@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}





























































































































.date-range-picker {
  position: relative;

  &__popup {
    position: absolute;
    top: 40px;
    padding: 10px;
    width: 800px;
    box-shadow: 2px 2px 5px rgba(#000, 0.1);
  }
}
