@charset "utf-8";

@import "../assets/scss/_webfont";
@import "../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}













































.home {
  margin-right: auto;
  margin-left: auto;
  padding-top: 100px;
  max-width: 800px;

  h2 {
    margin-bottom: 30px;
    font-weight: bold;
    font-size: 36px;
    &:not(:first-child) {
      margin-top: 45px;
    }
  }
}
