@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}









































































































































.date-picker-input {
  position: relative;

  &__field {
    padding: 0 10px;
    width: 100%;
    height: 30px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 13px;
    line-height: 30px;
    box-shadow: 0 0 3px rgba(#3468eb, 0);
    transition: box-shadow 0.3s ease, border-color 0.3s ease;
    &:disabled {
      background-color: #eee;
    }

    &--focus {
      border-color: #cfdcff;
      box-shadow: 0 0 3px rgba(#3468eb, 0.7);
    }
  }
  &__popup {
    position: absolute;
    top: 40px;
    left: 50%;
    padding: 10px;
    width: 100%;
    min-width: 700px;
    box-shadow: 2px 2px 5px rgba(#000, 0.1);
    transform: translateX(-50%);
  }
}

.datePickerPopup-enter-active,
.datePickerPopup-leave-active {
  opacity: 1;
  margin-top: 0;
  transition: opacity 0.3s ease, margin-top 0.3s ease;
}
.datePickerPopup-enter,
.datePickerPopup-leave-to {
  opacity: 0;
  margin-top: -40px;
}
