@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}














































































$cellHeight: 35px;

.calendar-base {
  &__title {
    display: flex;
    align-items: center;
    justify-content: center;
    height: $cellHeight;
    border-bottom: 1px solid #eee;
    text-align: center;
    font-weight: bold;
  }

  &__body {
    width: 100%;
    color: #111;
    table-layout: fixed;
  }

  &__cell {
    height: $cellHeight;
    vertical-align: middle;
    text-align: center;
    font-size: 12px;

    &--sun {
      color: #eb4034;
    }

    &--sat {
      color: #3468eb;
    }

    &--another-month {
      color: #ccc;
    }
  }
}
