@charset "utf-8";

@import "../assets/scss/_webfont";
@import "../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}








































































































.period-specification {
  &__directly-select {
    display: flex;
    margin-top: 10px;
  }

  &__button {
    padding: 5px;
    min-width: 85px;
    border: 1px solid #ddd;
    border-radius: 5px;
    background-color: #fff;
    text-align: center;
    cursor: pointer;

    &:hover {
      background-color: #f2f5fc;
    }

    &:not(:first-child) {
      margin-left: 5px;
    }

    &--current {
      background-color: #f2f5fc;
      font-weight: bold;
      cursor: text;
    }
  }
}
