@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}









































































.date-range-picker-popup {
  position: relative;
  background-color: #fff;

  &__button {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 0;
    padding: 5px;
    border: 1px solid #ddd;
    border-radius: 50%;
    background-color: #fff;
    font-size: 12px;
    line-height: 1;
    cursor: pointer;
    transition: border-color 0.3s ease, background-color 0.3s ease;

    &::before {
      width: 7px;
      height: 7px;
      border-top: 2px solid #ccc;
      position: absolute;
      top: 50%;
      content: "";
      transition: border-color 0.3s ease;
    }

    &:hover {
      background-color: #cfdcff;
      border-color: #cfdcff;

      &::before {
        border-color: #fff;
      }
    }
  }

  &__previous {
    left: 0;

    &::before {
      left: 50%;
      border-left: 2px solid #ccc;
      transform: translate(-50%, -50%) rotate(-45deg);
      margin-left: 5%;
    }
  }

  &__next {
    right: 0;

    &::before {
      right: 50%;
      border-right: 2px solid #ccc;
      transform: translate(50%, -50%) rotate(45deg);
      margin-right: 3%;
    }
  }

  &__body {
    display: flex;
    align-items: center;
  }

  &__calendar {
    &:not(:first-child) {
      margin-left: 20px;
    }
  }
}
