@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}






























































































































































$cellHeight: 35px;

.calendar-date-range-table {
  &__inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: $cellHeight;

    &:not(.calendar-date-range-table__inner--desable):hover {
      .calendar-date-range-table__date:not(.calendar-date-range-table__date--selected) {
        background-color: #cfdcff;
        cursor: pointer;
      }
    }

    &--desable {
      background-color: #fbfbfb;
      cursor: text;
      pointer-events: none;
    }

    &:before {
      position: absolute;
      top: 50%;
      z-index: 0;
      margin-top: -12px;
      height: 24px;
      background-color: #f0f5ff;
      content: "";
    }

    &--range {
      &:before {
        left: 0;
        width: 100%;
      }
    }

    &--range-start {
      &:before {
        right: 0;
        width: 50%;
      }
    }

    &--range-end {
      &:before {
        left: 0;
        width: 50%;
      }
    }
  }

  &__date {
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 24px;
    height: 24px;
    border: 2px solid rgba(0, 0, 0, 0);
    border-radius: 50%;
    transition: background-color 0.3s ease;

    &--today {
      border-color: #7a9aeb;
      color: #3468eb;
    }

    &--selected {
      background-color: #3468eb;
      color: #fff;
    }
  }
}
