@charset "utf-8";

@import "../../assets/scss/_webfont";
@import "../../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}











































































.date-range-picker-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1px;
  border: 1px solid #ddd;
  border-radius: 5px;

  &--disabled {
    background-color: #eee;
  }

  &__input {
    flex: 1;
    height: 30px;
    border: 2px solid rgba(0, 0, 0, 0);
    border-radius: 5px;
    text-align: center;
    font-size: 13px;
    line-height: 30px;
    &:disabled {
      background-color: #eee;
    }

    &--focus {
      border-color: #3468eb;
    }
  }

  &__hyphen {
    padding: 0 10px;
    width: 30px;
    text-align: center;
    line-height: 1;
  }
}
