@charset "utf-8";

@import "../assets/scss/_webfont";
@import "../assets/scss/_element-ui";

/*
 Layout
*/

#app {
  position: relative;
}

#root,
#app,
.modal-wrapper {
  color: #333;
  font-size: 14px;
}

[draggable="true"] {
  cursor: move;
  // drag中の要素を選択できないようにする

  user-select: none;
}

textarea {
  font-family: inherit;
}




























































































































































.dateRangePicker {
  display: flex;
  align-items: center;
  padding: 0 10px;
  border: 1px solid;
  border-radius: 5px;

  &:not(.dateRangePicker__disabled) {
    border-color: #ddd;
    color: #333;
  }

  &:not(.dateRangePicker__disabled):hover {
    border-color: #ccc;
  }
}

.dateRangePicker__disabled {
  border-color: #d6d6d6;
  background-color: #eee;
  color: #ccc;
}

.dateRangePicker__icon {
  padding-right: 10px;
}

.dateRangePicker__input {
  width: 100%;

  // element-ui をカスタマイズ
  /deep/ .el-input__prefix {
    display: none;
  }
  /deep/ .el-input__suffix {
    display: none;
  }
  /deep/ .el-input__inner {
    padding: 0;
    border: none;
    color: #333;
    text-align: center;
    cursor: pointer;

    &::placeholder {
      color: #999;
    }

    &:disabled {
      background-color: #eee;
      color: #ccc;
      cursor: default;
    }

    &:focus {
      background-color: #f2f5fc;
    }
  }
}

.dateRangePicker__hyphen {
  padding: 0 10px;
}
